:root {
  --color-black: #000;
  --color-white: #fff;
  --color-purple: #dd32b3;
  --color-blue: #2e3bf0;
  --color-dark: #000840;
}

@font-face {
  font-family: "Roboto Mono";
  src: url("../fonts/Roboto Mono-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Space Mono";
  src: url("../fonts/Space Mono-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Space Mono Normal";
  src: url("../fonts/Space Mono Normal-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

